.footer {
  background: #16212c;
  background: linear-gradient(
180deg
, #1f2f3c, #16212c);
  z-index: 9;
  position: relative;
}
.footer-grid {
  padding: 80px 0px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.bottom-footer {
  display: flex;
  justify-content: space-between;
  padding: 20px 0px 44px;
}

.bottom-footer ul {
  display: flex;
  flex-wrap: wrap;
}

.footer-column h3 {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #fff;
  margin: 0;
  padding-bottom: 25px;
}

.footer-column ul li a {
  font-weight: 300;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  display: block;
}

.footer-column ul li a:hover,
.footer-column ul li a:focus {
  color: #fff;
  text-decoration: none;
}

.footer-column ul li {
  padding-bottom: 15px;
}

.auth-menu li span,
.auth-menu li a {
  font-weight: 300;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  opacity: 0.6;
}

ul.social-icon li {
  margin-right: 24px;
}

ul.auth-menu li {
  margin-left: 75px;
}

ul.auth-menu li a:hover,
ul.auth-menu li a:focus {
  text-decoration: none;
  color: #fff;
  opacity: 1;
}

.footer-logo {
  display: none;
}

ul.social-icon li a img:last-child {
  position: absolute;
  left: 50%;
  top: 50%;
  opacity: 1;
  transform: translate(-50%, -50%);
  opacity: 0.6;
  transition: all 0.5s;
}

ul.social-icon li a img:hover {
  opacity: 1;
}
ul.social-icon li a {
  position: relative;
  width: 20px;
  height: 20px;
  display: block;
}

ul.social-icon li a img:first-child {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

ul.social-icon li a:hover img:first-child {
  opacity: 1;
}

ul.social-icon li a:hover img:last-child {
  opacity: 1;
}

.footer-column p {
  margin: 0;
  font-weight: 300;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
  display: block;
  padding-top: 15px;
  max-width: 320px;
  line-height: 1.8;
}

.footer-column img {
  width: 224px;
}

/*************** Media Query ****************/

@media screen and (max-width: 991px) {
  .footer-grid {
    padding: 20px 0px;
  }

  .footer-column {
    width: 48%;
    padding: 20px 0px;
  }

  .footer-grid .footer-column:first-child {
    width: 100%;
  }

  .bottom-footer {
    flex-wrap: wrap;
    justify-content: center;
    padding: 0px 0px 30px;
  }

  ul.auth-menu li {
    margin: 0;
    margin-top: 15px;
  }

  .bottom-footer ul {
    width: 100%;
    justify-content: center;
  }

  ul.social-icon li {
    margin-left: 12px;
    margin-right: 12px;
  }

  .footer-column p {
    max-width: 100%;
  }
}
